<template>
  <div class="register_box">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
      <el-form-item prop="username">
        <el-input
          v-model="ruleForm.username"
          :placeholder="i18n.$t('rules').user"
          class="row"
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          v-model="ruleForm.password"
          :placeholder="i18n.$t('rules').password"
          class="row"
          show-password
        ></el-input>
      </el-form-item>
    </el-form>
    <el-button
      class="row color"
      type="success"
      @click="submitForm('ruleForm')"
      :loading="flag"
      >{{ i18n.$t("common").submit }}</el-button
    >
  </div>
</template>

<script>
import { Api } from "../../../api";
import { useI18n } from "../../../i18nPlugin";
import { ElMessage } from "element-plus";
export default {
  data() {
    return {
      flag: false,
      ruleForm: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          {
            required: true,
            message: this.i18n.$t("rules").user,
            trigger: "blur",
          },
          {
            min: 3,
            max: 16,
            message: this.i18n.$t("rules").usermessage,
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: this.i18n.$t("rules").password,
            trigger: "change",
          },
          {
            min: 8,
            max: 16,
            message: this.i18n.$t("rules").passwordmessage,
            trigger: "blur",
          },
        ],
      },
    };
  },
  setup() {
    const i18n = useI18n();
    return {
      i18n,
    };
  },
  mounted() {},
  props: ["form"],
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.flag = true;
          let obj = {
            ...this.form,
            username: this.ruleForm.username,
            password: this.ruleForm.password,
          };
          Api.register_account(obj)
            .then((res) => {
              console.log(res);
              if (res.data.code == 200) {
                ElMessage.success({
                  type: "success",
                  message: this.i18n.$t("common").success,
                });
                this.$router.push(
                  "/mobile/success?password=" +
                    this.ruleForm.password +
                    "&email=" +
                    this.form.email
                );
              }
              this.flag = false;
              if (res.data.code == 400) {
                ElMessage.error(this.i18n.$t("common").networkErr);
              }
            })
            .catch((err) => {
              console.log(err.response);
              if (err.response.data.code == 400) {
                ElMessage.error(this.i18n.$t("common").networkErr);
              }
              if (err.response.data.code == 2003) {
                ElMessage.error(this.i18n.$t("common").emaillErr);
              }
              if (err.response.data.code == 2006) {
                ElMessage.error(this.i18n.$t("Businesses").errname);
              }
              this.flag = false;
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.register_box {
  width: 300px;
  min-height: 100px;
  padding: 20px;
  background-color: #ffffff;
  margin-top: 70px;
  border: 1px solid #dcdfe6;
  border-radius: 15px;
  margin-left: 50%;
  transform: translateX(-50%);
  .row {
    margin: 10px 0px;
    width: 100%;
  }
}
.el-form-item {
  margin: 22px 0;
}
.color {
  background-color: #68bf99;
  border: 1px solid #1fab89;
}
</style>
